.skill {
  display: flex;
  font-family: "Poppins", sans-serif;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  align-items: center;
  padding: 5em 0;
  margin: 0em auto;
  row-gap: 1em;
  &.dark {
    .skillset {
      .frontend {
        h4 {
          color: #ffff;
        }
      }
    }

    background-color: #0d0d0d;
    .lang{
      background-color: #1818181e;
    }
  }
  h1 {
    font-size: 3rem;
    // font-weight: 600;
    text-transform: uppercase;
    color: #0697ff;
  }
  .skillset {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    row-gap: 2em;
    max-width: 800px;
    justify-content: center;
    align-items: center;
    column-gap: 5em;
    .frontend {
      // background-color: #0697ff;
      padding: 1em;
      row-gap: 1em;
      flex: 1;
      display: flex;
      flex-direction: column;
      width: 100%;
      justify-content: center;
      h4 {
        color: #1e1e1e;
        font-size: 1.4rem;
        font-weight: 300;
      }
    }
  }
  .lang{
    display: flex;
    flex-wrap: wrap;
    row-gap: 1em;
    margin-top: 2em;
    background-color: #f2f4f542;
    width: 100%;
    justify-content: center;
    column-gap: 1em;
    img{
      width: 5em;
      object-fit: contain;
    }
  }
}
